    <!-- 定级结果弹窗(指标) -->

    <!-- 定级结果弹窗(安全族) -->
    <el-dialog title="定级结果(安全族)" width="80%" style="height: 80%" :visible.sync="dialogClassificationVisiable_family">
      <el-table :data="formServiceClassification_family" height="400" border style="width: 100%;height: 100%;"
                :span-method="spanMethod_classification_2">
        <el-table-column prop="grouplevelname" label="安全族">
        </el-table-column>
        <el-table-column label="等级">
          <template  slot-scope="scope">
            <span v-if="scope.row.target_level == 1">基础定义级</span>
            <span v-if="scope.row.target_level == 2">增强控制级</span>
            <span v-if="scope.row.target_level == 3">持续优化级</span>
            <span v-if="scope.row.target_level == 4">量化完善级</span>
          </template>
        </el-table-column>
        <el-table-column prop="group_score" label="得分">
        </el-table-column>
        <el-table-column prop="level_score" label="等级">
