    <!-- 定级结果弹窗(安全族) -->

    <!-- 定级结果弹窗(SAG) -->
    <el-dialog title="定级结果(综合)" width="80%" style="height: 80%" :visible.sync="dialogClassificationVisiable_sag">
      <el-table :data="formServiceClassification_sag" height="400" border style="width: 100%;height: 100%;"
                :span-method="spanMethod_classification_3">
        <!--<el-table-column prop="saglevelname" label="SAG类别"></el-table-column>-->
        <el-table-column label="SAG类别">
          <template slot-scope="scope">
            <el-tooltip placement="right">
              <!--<div slot="content">-->
                <!--<p v-for="(item,i ) in scope.row.sagdescription">{{item}}</p>-->
              <!--</div>-->
              <el-button>{{ scope.row.saglevelname }}</el-button>
              {{ scope.row.saglevelname }}
            </el-tooltip>
          </template>
        </el-table-column>

        <el-table-column label="等级">
          <template  slot-scope="scope">
            <span v-if="scope.row.target_level == 1">基础定义级</span>
            <span v-if="scope.row.target_level == 2">增强控制级</span>
            <span v-if="scope.row.target_level == 3">持续优化级</span>
            <span v-if="scope.row.target_level == 4">量化完善级</span>
          </template>
        </el-table-column>
        <el-table-column prop="sag_score" label="得分">
        </el-table-column>
        <el-table-column prop="level_score" label="等级">
        </el-table-column>
