    <!-- 定级结果弹窗(SAG) -->

    <!-- 删除对话框 -->
    <Dialog ref="dialogDeleteData"
            :title="'删除确认'"
            :dialogStyle="{width:'400px',height:'200px'}"
            bodyCls="f-column"
            :closable=false
            :modal="true"
            :draggable="true"
            :resizable="true">
      <div class="f-full">
        <p style="text-align:center;margin:20px 0;font-size:16px">是否删除服务"{{ dialogDeleteData.servicename }}"以及相关评测数据？</p>
      </div>
      <div class="dialog-button">
        <LinkButton style="width:80px" @click="callOperation(true, dialogDeleteData.serviceid)">确定</LinkButton>
        <LinkButton style="width:80px" @click="callOperation(false, dialogDeleteData.serviceid)">关闭</LinkButton>
      </div>
    </Dialog>
    <!-- 删除对话框 -->

  </d2-container>
</template>

<script>
  import { RetrieveServiceEvaluation, UpdateServiceEvaluation } from '@/api/demo/service_evaluation'
  import { GetServiceInfo, DeleteServiceInfo, CreateServiceInfo, CreateServiceInfoDetail } from '@/api/demo/service_info'
  import { GetServiceEvaluation, CreateServiceEvaluation } from '@/api/demo/service_evaluation'
  import { GetServiceClassification, GetServiceClassificationTarget } from '@/api/demo/service_classification'
  import { GetIndexInfo } from '@/api/demo/index_info'

  export default {
    name: 'demo-evaluation-index1',
    inject:['reload'],
    data () {
      return {
        total: 15,
        pageSize: 10,
        pagePosition: 'bottom',
        serviceTable: [],
        // 指标项
        index_base: null,
        // 服务信息
        formServiceInfo: {
          servicetype: '数据服务',
        },
        servicetypeword: this.getServicetypeword(),
        dialogServiceInfoVisiable: false,
        dialogServiceInfoAddVisiable: false,
        service_info_labelPosition: 'right',
        dialogDeleteData: {
          servicename: '',
          serviceid: 0
        },
        // 服务测评内容
        formEvaluation: null,
        formEvaluationAdd: null,
        formEvaluationUpdate: null,
        dialogEvaluationVisiable: false,
        dialogEvaluationAddVisiable: false,
        dialogEvaluationUpdateVisiable: false,
        // 服务定级结果
        dialogClassificationVisiable_target: false,
        dialogClassificationVisiable_family: false,
        dialogClassificationVisiable_sag: false,
        formServiceClassification_target: null,
        formServiceClassification_family: null,
        formServiceClassification_sag: null,
        // 对话框
        dialogDeleteVisiable: false,
      }
    },
    created () {
      // 从后台获取问题对象
      this.createServiceTable()
      this.getIndex()
    },
    mounted () {
      this.$refs.dialogDeleteData.close()
    },
    methods: {
      getServicetypeword: function () {
        return [
          { value: '数据服务', text: "数据服务" },
          { value: '计算服务', text: "计算服务" },
          { value: '网络服务', text: "网络服务" },
          { value: '社交通讯服务', text: "社交通讯服务" },
          { value: '信息检索服务', text: "信息检索服务" },
          { value: '信息管理服务', text: "信息管理服务" },
          { value: '交易支付服务', text: "交易支付服务" },
          { value: '多媒体服务安', text: "多媒体服务" },
          { value: '信息发布订阅服务', text: "信息发布订阅服务" }
        ];
      },

      createServiceTable: function () {
        RetrieveServiceEvaluation()
          .then(async res => {
            const responseData = res.filter(e => e.deletestate == '1');
            this.serviceTable = responseData
          })
          .catch(err => {
            console.log('err: ', err)
          })
      },

      // 获取 index指标项全集
      getIndex: function() {
        GetIndexInfo()
          .then(async res => {
            const responseData = res;
            this.index_base = responseData
          })
          .catch(err => {
            console.log('err: ', err)
          })
      },

      // 添加service
      addService: function(){
        this.formServiceInfo = {
          testorganization: '无',
          cloudservicetype: '无',
          businessscope: '无',
          companyname: '无',
          companytype: '无',
          ip: '无',
          legalperson: '无',
          linkman: '无',
          linkmantel: '无',
          password: '无',
          port: '无',
          protocol: '无',
          regaddress: '无',
          regcapital: '无',
          regdate: '无',
          serviceintro: '无',
          servicelocation: '无',
          serviceobject: '无',
          serviceterm: '无',
          servicetype: '无',
          serviceurl: '无',
          suic: '无',
          username: '无',
          term: '无',
          username: '无',
          servicetype: '数据服务',
        };
        this.dialogServiceInfoAddVisiable = true;
      },

      // 呼出删除确认框
      callDelete: function(currentRow) {
        this.dialogDeleteData = currentRow;
        setTimeout(() => {
          this.$refs.dialogDeleteData.open()
        }, 10)
      },

      // 删除操作
      callOperation: function(opt, para) {
        if (opt){
          DeleteServiceInfo(para).then(res => {
            if (res === "成功") {
              console.log("删除成功")
              this.reload()
            } else {
              alert("删除异常，请联系管理员。")
            }
          }).catch(err => {
            console.log('err: ', err)
          })
        }
        this.$refs.dialogDeleteData.close();
      },

      // 提交添加的服务
      submitService: function() {
        var serviceInfo = this.formServiceInfo
        var basic_service = {
          servicename: serviceInfo.servicename,
          servicecompany: serviceInfo.companyname,
          servicetype: serviceInfo.servicetype,
          testorganization: serviceInfo.testorganization
        };
        if (basic_service.servicename === undefined ||
          basic_service.servicecompany === undefined ||
          basic_service.servicetype === undefined ||
          basic_service.testorganization === undefined) {
          alert("请填写必要信息！")
          return
        };

        CreateServiceInfo(basic_service).then(res => {
          var serviceid_return = res.serviceid

          var detail_service = {
            serviceid: serviceid_return,
            servicename: serviceInfo.servicename,
            businessscope: serviceInfo.businessscope,
            companyname: serviceInfo.companyname,
            companytype: serviceInfo.companytype,
            ip: serviceInfo.ip,
            legalperson: serviceInfo.legalperson,
            linkman: serviceInfo.linkman,
            linkmantel: serviceInfo.linkmantel,
            password: serviceInfo.password,
            port: serviceInfo.port,
            protocol: serviceInfo.protocol,
            regaddress: serviceInfo.regaddress,
            regcapital: serviceInfo.regcapital,
            regdate: serviceInfo.regdate,
            serviceintro: serviceInfo.serviceintro,
            servicelocation: serviceInfo.servicelocation,
            serviceobject: serviceInfo.serviceobject,
            serviceterm: serviceInfo.serviceterm,
            servicetype: serviceInfo.cloudservicetype,
            serviceurl: serviceInfo.serviceurl,
            suic: serviceInfo.suic,
            username: serviceInfo.username,
            term: serviceInfo.term,
            username: serviceInfo.username,
          };
          CreateServiceInfoDetail(detail_service)
            .then(res => {
              var servicedetailid_return = res.servicedetailid
              this.dialogServiceInfoAddVisiable = false
            })
            .catch(err => {
              console.log('err: ', err)
            })

        }).catch(err => {
          console.log('err: ', err)
        })
        this.reload()
      },

      // 查询服务信息
      callServiceInfo: function (currentRow) {
        GetServiceInfo()
          .then(async res => {
            const responseData = res.filter(e => e.serviceid == currentRow['serviceid']);
            this.formServiceInfo = responseData[0];
            this.dialogServiceInfoVisiable = true;
          })
          .catch(err => {
            console.log('err: ', err)
          })
      },

      // 查询测评内容
      callEvaluation: function (currentRow) {
        let service_evaluation_id = currentRow["serviceid"];
        GetServiceEvaluation(service_evaluation_id)
          .then(async res => {
            let temp = res.filter(e => e.serviceid === service_evaluation_id)[0];
            var index_copy = this.index_base.map(function (value, index) {
              var index_result = temp['result' + (index+1)].split(",")
              var value_1 = JSON.parse(JSON.stringify(value))
              var value_2 = JSON.parse(JSON.stringify(value))
              var value_3 = JSON.parse(JSON.stringify(value))
              var value_4 = JSON.parse(JSON.stringify(value))

              value_1.target_level = 1
              value_2.target_level = 2
              value_3.target_level = 3
              value_4.target_level = 4

              value_1.level_score = index_result[0] + '/' + index_result[1]
              value_2.level_score = index_result[2] + '/' + index_result[3]
              value_3.level_score = index_result[4] + '/' + index_result[5]
              value_4.level_score = index_result[6] + '/' + index_result[7]

              value_1.index_score = index_result[8]
              value_2.index_score = index_result[8]
              value_3.index_score = index_result[8]
              value_4.index_score = index_result[8]
              return [value_1, value_2, value_3, value_4]
            })

            this.formEvaluation = index_copy.flat();
            this.dialogEvaluationVisiable = true;
          })
          .catch(err => {
            console.log('err: ', err)
          })
      },

      // 添加测评内容
      addEvaluation: function (currentRow) {
        let service_evaluation_id = currentRow["serviceid"];
        this.formEvaluationAdd = null
        var index_copy = this.index_base.map(function (value, index) {
            value.serviceid = service_evaluation_id
            var value_1 = JSON.parse(JSON.stringify(value))
            var value_2 = JSON.parse(JSON.stringify(value))
            var value_3 = JSON.parse(JSON.stringify(value))
            var value_4 = JSON.parse(JSON.stringify(value))

            value_1.target_level = 1
            value_2.target_level = 2
            value_3.target_level = 3
            value_4.target_level = 4

            value_1.numerator = 0
            value_1.denominator = 0
            value_2.numerator = 0
            value_2.denominator = 0
            value_3.numerator = 0
            value_3.denominator = 0
            value_4.numerator = 0
            value_4.denominator = 0

          return [value_1, value_2, value_3, value_4]
        })

        this.formEvaluationAdd = index_copy.flat();
        this.dialogEvaluationAddVisiable = true
      },

      // 更新测评内容
      updateEvaluation: function (currentRow) {
        var service_evaluation_id = currentRow["serviceid"];
        this.formEvaluationUpdate = null
        GetServiceEvaluation(service_evaluation_id)
          .then(async res => {
            let temp = res.filter(e => e.serviceid === service_evaluation_id)[0];
            var index_copy = this.index_base.map(function (value, index) {
              var index_result = temp['result' + (index+1)].split(",")
              value.serviceid = temp.serviceid
              value.testresultid = temp.testresultid
              var value_1 = JSON.parse(JSON.stringify(value))
              var value_2 = JSON.parse(JSON.stringify(value))
              var value_3 = JSON.parse(JSON.stringify(value))
              var value_4 = JSON.parse(JSON.stringify(value))

              value_1.target_level = 1;
              value_2.target_level = 2;
              value_3.target_level = 3;
              value_4.target_level = 4;

              value_1.numerator = parseInt(index_result[0])
              value_1.denominator = parseInt(index_result[1])
              value_2.numerator = parseInt(index_result[2])
              value_2.denominator = parseInt(index_result[3])
              value_3.numerator = parseInt(index_result[4])
              value_3.denominator = parseInt(index_result[5])
              value_4.numerator = parseInt(index_result[6])
              value_4.denominator = parseInt(index_result[7])

              return [value_1, value_2, value_3, value_4]
            })

            this.formEvaluationUpdate = index_copy.flat();
            this.formEvaluationUpdate.map((value, index) => {
              value.serviceid = service_evaluation_id
              return value
            })
            this.dialogEvaluationUpdateVisiable = true;
          })
          .catch(err => {
            console.log('err: ', err)
          })
      },

      submitEvaluation: function () {
        var tempEvaluation = JSON.parse(JSON.stringify(this.formEvaluationAdd))
        var submitEvaluationData = {
          serviceid: tempEvaluation[0].serviceid,
          deletestate: 1
        }
        for (var i = 1; i <= 130; i++) {
          var result = ''
          var te1 = tempEvaluation.filter(e => e.indexid === i && e.target_level === 1)[0]
          result+= (te1.numerator + ',' + te1.denominator + ',')
          var te2 = tempEvaluation.filter(e => e.indexid === i && e.target_level === 2)[0]
          result += (te2.numerator + ',' + te2.denominator + ',')
          var te3 = tempEvaluation.filter(e => e.indexid === i && e.target_level === 3)[0]
          result += (te3.numerator + ',' + te3.denominator + ',')
          var te4 = tempEvaluation.filter(e => e.indexid === i && e.target_level === 4)[0]
          result += (te4.numerator + ',' + te4.denominator)
          submitEvaluationData['result' + i] = result
        }
        CreateServiceEvaluation(submitEvaluationData)
          .then(res => {
            this.dialogEvaluationAddVisiable = false
            this.reload()
        })
          .catch(err => {
            console.log('err: ', err)
        })
      },

      submitEvaluationUpdate: function () {
        var tempEvaluation = JSON.parse(JSON.stringify(this.formEvaluationUpdate))
        var submitEvaluationData = {
          serviceid: tempEvaluation[0].serviceid,
          testresultid: tempEvaluation[0].testresultid,
        }
        for (var i = 1; i <= 130; i++) {
          var result = ''
          var te1 = tempEvaluation.filter(e => e.indexid === i && e.target_level === 1)[0]
          result+= (te1.numerator + ',' + te1.denominator + ',')
          var te2 = tempEvaluation.filter(e => e.indexid === i && e.target_level === 2)[0]
          result += (te2.numerator + ',' + te2.denominator + ',')
          var te3 = tempEvaluation.filter(e => e.indexid === i && e.target_level === 3)[0]
          result += (te3.numerator + ',' + te3.denominator + ',')
          var te4 = tempEvaluation.filter(e => e.indexid === i && e.target_level === 4)[0]
          result += (te4.numerator + ',' + te4.denominator)
          submitEvaluationData['result' + i] = result
        }
        UpdateServiceEvaluation(submitEvaluationData)
          .then(res => {
            this.dialogEvaluationUpdateVisiable = false
            this.reload()
          })
          .catch(err => {
            console.log('err: ', err)
          })
      },

      callClassification_target: function (currentRow) {
        let service_classification_id = currentRow["serviceid"];
        GetServiceEvaluation(service_classification_id)
          .then(async res => {
            let temp = res.filter(e => e.serviceid === service_classification_id)[0];
            var index_copy = this.index_base.map(function (value, index) {
              var index_result = temp['result' + (index+1)].split(",")
              var value_1 = JSON.parse(JSON.stringify(value))
              var value_2 = JSON.parse(JSON.stringify(value))
              var value_3 = JSON.parse(JSON.stringify(value))
              var value_4 = JSON.parse(JSON.stringify(value))

              value_1.target_level = 1
              value_2.target_level = 2
              value_3.target_level = 3
              value_4.target_level = 4

              value_1.level_score = index_result[0] + '/' + index_result[1]
              value_2.level_score = index_result[2] + '/' + index_result[3]
              value_3.level_score = index_result[4] + '/' + index_result[5]
              value_4.level_score = index_result[6] + '/' + index_result[7]

              value_1.index_score = index_result[8]
              value_2.index_score = index_result[8]
              value_3.index_score = index_result[8]
              value_4.index_score = index_result[8]
              return [value_1, value_2, value_3, value_4]
            })

            this.formServiceClassification_target = index_copy.flat();

            this.dialogClassificationVisiable_target = true;
          })
          .catch(err => {
            console.log('err: ', err)
          })
      },

      callClassification_family: function (currentRow) {
        let service_classification_id = currentRow["serviceid"]
        GetServiceClassification(service_classification_id, 'family')
          .then(async res => {
            let resfilter = res.filter(e => e.serviceid == service_classification_id)[0];
            let responseData = resfilter['23个分类得分的list'].map(function (value, index) {
              var group_result = value.score.split(",")

              var value_1 = JSON.parse(JSON.stringify(value))
              var value_2 = JSON.parse(JSON.stringify(value))
              var value_3 = JSON.parse(JSON.stringify(value))
              var value_4 = JSON.parse(JSON.stringify(value))

              value_1.target_level = 1
              value_2.target_level = 2
              value_3.target_level = 3
              value_4.target_level = 4

              value_1.group_score = group_result[0]
              value_2.group_score = group_result[1]
              value_3.group_score = group_result[2]
              value_4.group_score = group_result[3]

              value_1.level_score = group_result[4]
              value_2.level_score = group_result[4]
              value_3.level_score = group_result[4]
              value_4.level_score = group_result[4]
              return [value_1, value_2, value_3, value_4]
            })

            this.formServiceClassification_family = responseData.flat();
            this.dialogClassificationVisiable_family = true;
          })
          .catch(err => {
            console.log('err: ', err)
          })
      },

      callClassification_sag: function (currentRow) {
        let service_classification_id = currentRow["serviceid"];
        GetServiceClassification(service_classification_id, 'sag')
          .then(async res => {
            let resfilter = res.filter(e => e.serviceid == service_classification_id)[0];
            let responseData = [resfilter['S类'], resfilter['A类'], resfilter['G类']].map(function (value, index) {
              var sag_result = value.score.split(",")

              var value_1 = JSON.parse(JSON.stringify(value))
              var value_2 = JSON.parse(JSON.stringify(value))
              var value_3 = JSON.parse(JSON.stringify(value))
              var value_4 = JSON.parse(JSON.stringify(value))

              var des = value.sagdescription.split(',')

              value_1.sagdescription = des
              value_2.sagdescription = des
              value_3.sagdescription = des
              value_4.sagdescription = des

              value_1.target_level = 1
              value_2.target_level = 2
              value_3.target_level = 3
              value_4.target_level = 4

              value_1.sag_score = sag_result[0]
              value_2.sag_score = sag_result[1]
              value_3.sag_score = sag_result[2]
              value_4.sag_score = sag_result[3]

              value_1.level_score = sag_result[4]
              value_2.level_score = sag_result[4]
              value_3.level_score = sag_result[4]
              value_4.level_score = sag_result[4]
              return [value_1, value_2, value_3, value_4]
            })

            this.formServiceClassification_sag = responseData.flat();
            this.dialogClassificationVisiable_sag = true;
          })
          .catch(err => {
            console.log('err: ', err)
          })
      },

      exportExcel: function () {
        var wb = XLSX.utils.table_to_book(document.querySelector('#formEvaluation'));
        var wbout = XLSX.write(wb, { bookType: 'xlsx', bookSST: true, type: 'array' });
        try {
          FileSaver.saveAs(new Blob([wbout], { type: 'application/octet-stream' }), 'sheetjs.xlsx')
        } catch (e) {
          if (typeof console !== 'undefined')
            console.log(e, wbout)
        }
        return wbout
      },

      spanMethod: function ({ row, column, rowIndex, columnIndex } ) {
        if (columnIndex === 0 || columnIndex === 1 || columnIndex === 2 || columnIndex === 5) {
          if (rowIndex % 4 === 0) {
            return {
              rowspan: 4,
              colspan: 1
            };
          } else {
            return {
              rowspan: 0,
              colspan: 0
            };
          }
        }
      },

